<template>
  <div>
    <!-- 续费 -->
    <div class="pay_box">
      <div class="pay_top flex weight">
        <img class="mr8"
             src="@/assets/hotelAudit/cloud.png">
        <div>酒店云管理软件使用</div>
      </div>
      <div class="pay_bottom flex">
        <div>
          <div class="weight">剩余{{ diffDay }}天</div>
          <div v-if="current_info"
               class="c_b2">将于{{ current_info.endLineTime | dataFilter('YYYY年MM月DD日') }}到期</div>
        </div>
        <div class="button"
             @click="goNext('/continuePayChoose','pay')">续费</div>
      </div>
    </div>
    <!-- 扩容 -->
    <div class="pay_box">
      <div class="pay_top flex weight">
        <img class="mr8"
             src="@/assets/hotelAudit/room.png">
        <div>可使用房间数量</div>
      </div>
      <div class="pay_bottom flex">
        <div>
          <div v-if="current_info"
               class="weight"><span class="c_b2">{{ realRooms }}间</span>/共{{ current_info.roomCap }}间</div>
        </div>
        <div class="button"
             @click="goNext('/continuePayChoose','add')">扩容</div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessHotel } from '@/api/api_hotel.js';
import { GetBusinessRoomNumByhotel } from '@/api/api_soft';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            current_info: null,
            diffDay: null,
            realRooms: null
        };
    },
    created() {
        this.getHotelInfo();
        this.$store.commit('setHeader', '续费');
    },
    methods: {
        // 酒店信息
        getHotelInfo() {
            GetBusinessHotel({ hotelId: this.$route.query.hotelId }).then((res) => {
                this.current_info = res.data;
                this.diffDay = dayjs(this.current_info.endLineTime).diff(dayjs().format('YYYY-MM-DD'), 'day');
                this.getRooms();
            });
        },
        // 查询酒店房间数量
        getRooms() {
            GetBusinessRoomNumByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.realRooms = res.data;
            });
        },
        goNext(url, type) {
            this.$router.push({
                path: url,
                query: {
                    type: type,
                    round: type == 'add' ? '1' : '0',
                    endLineTime: this.current_info.endLineTime,
                    roomCap: this.current_info.roomCap,
                    charge: this.current_info.charge,
                    hotelId: this.$route.query.hotelId
                }
            });
        }
    }
};
</script>
<style scoped>
.pay_box {
    background: #fff;
    margin: 20px 6px;
    padding: 10px;
    font-size: 14px;
    text-align: left;
    box-shadow: 0px 3px 6px #e7e7e7;
    height: 100px;
}
.pay_top {
    padding: 14px 0;
}
.pay_bottom {
    justify-content: space-between;
    align-items: center;
    line-height: 25px;
}
.button {
    background: #ff7d41;
    color: #fff;
    border-radius: 20px;
    width: 15%;
    font-size: 12px;
    text-align: center;
}
.flex {
    display: flex;
}
.weight {
    font-weight: bold;
}
.mr8 {
    margin-right: 8px;
}
.c_b2 {
    color: #b2b2b2;
}
</style>



